import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import 'rxjs/add/operator/map';

interface LogsResponse {
    total: Number,
    limit: Number,
    skip: Number
    data: [{}];
}

@Injectable()
export class FeathersService {
    constructor(private httpClient: HttpClient) { }
    feathersGetUrl = 'http://localhost:3030/logs';

    getLogs() {
        return this.httpClient.get<LogsResponse>(this.feathersGetUrl)
            .map((res) => {
                return res.data;
            },
            (err: HttpErrorResponse) => {
                if (err.error instanceof Error) {
                    // A client-side or network error occurred. Handle it accordingly.
                    console.log('An error occurred:', err.error.message);
                } else {
                    // The backend returned an unsuccessful response code.
                    // The response body may contain clues as to what went wrong,
                    console.log(`Backend returned code ${err.status}, body was: ${err.error}`);
                }
            })
    }

    addLog(devData) {
        return this.httpClient.post(this.feathersGetUrl, devData)
        .map( data => data);
    }

    deleteProfile(_id) {
        return this.httpClient.delete(`${this.feathersGetUrl}/${_id}`)
        .map(res => res);
    }

}


